// app/(promos)/promociones/page.tsx
"use client";

import { useMemo } from "react";
import { useProductosPromo } from "@/hooks/useProductsPromo";
import PromoHeader from "./components/promoHeader";
import PromoContainer from "./components/promoContainer";
import { LoaderCircle, AlertTriangle } from "lucide-react";

export default function PromocionesPage() {
    // 1. Usar el hook central para obtener todos los productos con sus promos ya adaptadas.
    const { productos, loading, error } = useProductosPromo();

    // 2. Filtrar los productos que tienen una promoci√≥n v√°lida para hoy.
    const dia = new Date().getDay(); // 0 para Domingo, 1 para Lunes, etc.

    const productosEnPromoHoy = useMemo(() => {
        if (!productos) return [];
        return productos.filter(
            (p) => p.promo && p.promo.diasSemana.includes(dia)
        );
    }, [productos, dia]);

    // 3. Manejar estados de carga y error.
    if (loading) {
        return (
            <main className="min-h-screen flex items-center justify-center">
                <LoaderCircle className="w-12 h-12 animate-spin text-orange-500" />
            </main>
        );
    }

    if (error) {
        return (
            <main className="min-h-screen flex flex-col items-center justify-center text-red-500">
                <AlertTriangle className="w-16 h-16 mb-4" />
                <h2 className="text-2xl font-bold">
                    Error al cargar las promociones.
                </h2>
                <p>{error.message}</p>
            </main>
        );
    }

    // 4. Renderizar la p√°gina pasando los datos ya filtrados y listos para usar.
    return (
        <main className="min-h-screen bg-gradient-to-b from-yellow-100 to-orange-100 dark:from-gray-900 dark:to-gray-800 py-10 px-4">
            <PromoHeader dia={dia} />
            <PromoContainer productosEnPromo={productosEnPromoHoy} />
        </main>
    );
}
