// app/(promos)/promociones/components/ProductCard.tsx
import Image from "next/image";
import { Producto } from "@/lib/types";

interface Props {
    producto: Producto;
}

export default function ProductCard({ producto }: Props) {
    return (
        <div className="flex flex-col items-center w-full p-2 rounded-xl bg-yellow-50 dark:bg-gray-800 border-2 border-blue-200 dark:border-yellow-700">
            <Image
                src={producto.image}
                alt={producto.name}
                width={70}
                height={70}
                className="rounded-lg shadow-sm mb-2 bg-white"
            />
            <span className="text-center text-sm font-semibold text-gray-900 dark:text-yellow-200">
                {producto.name}
            </span>
            <span className="text-xs text-green-600 dark:text-green-300 mt-1">
                ${producto.price.toFixed(2)}
            </span>
        </div>
    );
}
