// app/(promos)/promociones/components/PromoCard.tsx
import { Promo, Producto } from "@/lib/types";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { PromoBadge } from "./promoBadge";
import ProductCard from "./productCard";

interface Props {
    promo: Promo;
    productos: Producto[];
}

export default function PromoCard({ promo, productos }: Props) {
    return (
        <Card className="border-yellow-400 hover:scale-105 transition-all duration-300">
            <CardHeader className="flex items-center justify-between">
                <CardTitle className="font-simpson text-blue-900 dark:text-yellow-400">
                    {promo.nombre}
                </CardTitle>
                <PromoBadge tipo={promo.tipo} valor={promo.valor} />
            </CardHeader>
            <CardContent className="flex flex-col gap-4">
                <p className="text-gray-700 dark:text-gray-300">
                    {promo.descripcion}
                </p>
                <div className="flex flex-wrap gap-4">
                    {productos.length === 0 ? (
                        <span className="text-sm italic text-gray-400">
                            No hay productos asociados
                        </span>
                    ) : (
                        productos.map((prod) => (
                            <ProductCard key={prod.id} producto={prod} />
                        ))
                    )}
                </div>
            </CardContent>
        </Card>
    );
}
