"use client";

import { useMemo } from "react";
import Image from "next/image";
import { ProductoConPromo, PromoAplicada } from "@/lib/types";
import PromoCard from "./promoCard";

interface Props {
    // Props simplificadas: Solo recibe la lista de productos ya filtrados.
    productosEnPromo: ProductoConPromo[];
}

export default function PromoContainer({ productosEnPromo }: Props) {
    // Agrupar productos por promoci√≥n para poder renderizar una card por cada promo activa.
    const promosAgrupadas = useMemo(() => {
        const groups: Map<
            number,
            { promo: PromoAplicada; productos: ProductoConPromo[] }
        > = new Map();

        productosEnPromo.forEach((producto) => {
            // Si el producto no tiene promo, no deber√≠a estar en esta lista, pero lo verificamos por seguridad.
            if (!producto.promo) return;

            const promoId = producto.promo.id;

            if (!groups.has(promoId)) {
                groups.set(promoId, { promo: producto.promo, productos: [] });
            }
            groups.get(promoId)!.productos.push(producto);
        });

        return Array.from(groups.values());
    }, [productosEnPromo]);

    // Si no hay productos en promoci√≥n para hoy, muestra el mensaje de Apu.
    if (productosEnPromo.length === 0) {
        return (
            <div className="flex flex-col items-center mt-24">
                <Image
                    src="/images/apu-triste.png"
                    alt="Apu triste"
                    width={160}
                    height={160}
                    className="mb-4"
                />
                <h2 className="text-2xl font-simpson text-gray-700 dark:text-gray-200">
                    ¬°Hoy no hay promociones activas!
                </h2>
                <p className="text-lg text-gray-600 dark:text-gray-400 mt-2">
                    ‚ÄúMuchas gracias, vuelva pronto‚Ä¶‚Äù
                </p>
            </div>
        );
    }

    // Mapea sobre las promociones ya agrupadas.
    return (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 xl:grid-cols-3 gap-8 max-w-6xl mx-auto">
            {promosAgrupadas.map(({ promo, productos }) => (
                <PromoCard key={promo.id} promo={promo} productos={productos} />
            ))}
        </div>
    );
}
